import { ApolloClient, InMemoryCache, gql } from "@apollo/client";
// @ts-ignore
import { v4 as uuid } from "uuid";

export function getBookings() {
  const client = new ApolloClient({
    uri: "https://localhost/graphql",
    cache: new InMemoryCache(),
  });

  return client
    .query({
      query: gql`
        query {
          allBookings {
            bookingId
            name
            email
            request
            state
          }
        }
      `,
    })
    .then((result) => {
      const { data: allBookings } = result;
      return { bookings: allBookings.allBookings };
    });
}

export function updateBooking(id: string, state: string, password: string) {
  const client = new ApolloClient({
    uri: "https://localhost/graphql",
    cache: new InMemoryCache(),
    headers: {
      password: password,
    },
  });

  const update = `
  mutation UpdateBooking {
     updateBookingOne(filter: {bookingId: "${id}"} record: { state: "${state}"})  {
        record {
            state
        }

    }
}`;

  client.mutate({
    mutation: gql`
      ${update}
    `,
  });
}

export function createBooking(name?: string, email?: string, request?: string) {
  const id = uuid();

  const client = new ApolloClient({
    uri: "https://localhost/graphql",
    cache: new InMemoryCache(),
  });

  const mutation = `
        mutation CreateBooking {
          bookingCreateOne(
            record: {
              bookingId: "${id}"
              name: "${name}"
              email: "${email}"
              request: "${request}"
              state: "placed"
            }
          ) {
            record {
              bookingId
              email
              request
              state
            }
          }
        }
      `;

  client.mutate({
    mutation: gql`
      ${mutation}
    `,
  });
}
